@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin content-padding-inline {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
  }

  @include on-desktop {
    max-width: 1024px;
    padding-inline: 120px;
    margin-inline: auto;
  }
}

.container {
  @include content-padding-inline;
}

@mixin hover($property, $toValue) {
  transition: #{$property} 0.3s;

  &:hover {
     #{$property}: $toValue;
  }
}

@mixin page-grid {
  --columns: 2;

    display: grid;
    column-gap: 20px;
    grid-template-columns: repeat(var(--columns), 1fr);

    @include on-tablet {
      column-gap: 24px;
      
      --columns: 6;
    }

    @include on-desktop {
      --columns: 12;
    }
}

@mixin title-gap {
  display: grid;
  row-gap: 48px;
}
